<template>
<div class="home infobg">
	<header>
			<div><img src="../../../assets/images/hylb.png"></div>
			<div>消息</div>
			<div><img src="../../../assets/images/add.png"></div>
	</header>
	<div class="homeContent">
		<div>
			<input type="text" placeholder="搜索">
		</div>
			<router-link :to="{name: 'Chat'}">
				<div  @touchstart="showbackground()" @touchend="clearLoop()" :class="{ 'theActive': isActive }" class="infoShow" >
					<div class="infoShowBox">
						<div class="userIcon">
							<img src="../../../assets/images/Icon.jpg">
						</div>
						<div class="UserId">
							<div class="userName">
								oldchin
							</div>
							<div class="userInfo">
								你好呀
							</div>
						</div>
						<div class="infoTime">
							昨天
						</div>
					</div>
				</div>
			</router-link>
	</div>
</div>
</template>

<script>
	export default{
		name: 'Info',
		data(){
			return{
				isActive: false,
			}
		},
		methods:{
			showbackground(){
				var that = this;
				this.loop = setTimeout(function() {
					that.isActive = true;
				},200);
			},
			clearLoop(){
				clearInterval(this.loop);
				this.isActive = false;
			}
		}
	
}
</script>

<style lang="scss">
.infobg{
	background:rgba(0,0,0,.1);
}
.homeContent > div{
	background:white;
}

.infoShow{
	width: 100%;
	height: 6rem;
	padding-top: 1rem;
	padding-left:4%;
	box-sizing:border-box;
	background:white;
	.infoShowBox{
		width: 100%;
		height: 4rem;
		display: flex;
		color: black;
		.userIcon{
			width: 4rem;
			height: 4rem;
			flex-shrink: 0;
			border-radius: 50%;
			overflow: hidden;
			img{
				width: 4rem;
				height: 4rem;
				text-align: center;
			}
		}	
		.UserId{
			width: 100%;
			height: 100%;
			font-size: 1.6rem;
			padding-left:4%;
			box-sizing:border-box;
			position:relative;
